import React, {useEffect, useState} from 'react';
import Gallery from "./gallery.js"
function App(){
  const [user] = useState([
    {name: 'Danaus Plexippus', picture: './pictures/Butterfly2.jpg'},
    {name: 'Morpho Pelides', picture: 'pictures/Butterfly2.jpg'},
    {name: 'Delias Eucharia', picture: 'pictures/Butterfly3.jpg'},

  ]);

  const [quote, setQuote] = useState("");
  // https://quotes.rest/qod.json?category=inspire

  useEffect(() => {
    fetch("https://quotes.rest/qod.json?category=inspire")
    .then(res => res.json())
    .then(
      (quote) => {
        setQuote(quote.contents.quotes[0].quote)
        console.log(quote)
      }    
    )
  },[]);

  return (

    <div className="app">
      <div className="headers">
        <h1>Butterflies</h1>
        <h1></h1>
        <h1></h1>
      </div>
      <div className="quotes">
        <p>{quote}</p>
        <p></p>
        <p></p>
      </div>
      <div className="tweets">
        {user.map(user => (
          <Gallery name={user.name} picture={user.picture}/>
        ))}
      </div>
    </div>
  );
}

export default App;